          </div>
        </div>
      </div>
      <div class="client-say">
        <img class="bg" src="../../../public/static/img/mobile/client_bg.png" alt="">
        <h2 class="title">客户这么说</h2>
        <div 
          class="client-item"
          :class="client1.style"
          @touchstart="ontouchstart($event, 1)"
          @touchmove="ontouchmove($event, 1)"
          @touchend="ontouchend($event, 1)"
          draggable="true"
          >
          <img :src="client1.bg" alt="">
          <div class="top">
            <span>上海泛远</span>
            <span>黎女士</span>
          </div>
          <div class="text">
            <p>非常方便，每天都在使用，</p>
            <p>极大的提升了工作效率，</p>
            <p>系统太棒了</p>
          </div>
        </div>
        <div 
          class="client-item" 
          :class="client2.style"
          @touchstart="ontouchstart($event, 2)"
          @touchmove="ontouchmove($event, 2)"
          @touchend="ontouchend($event, 2)"
          draggable="true"
         >
          <img :src="client2.bg" alt="">
          <div class="top">
            <span>深圳鹏程</span>
            <span>王先生</span>
          </div>
          <div class="text">
            <p>操作可视化，系统自动跟踪</p>
            <p>和提醒每票单子状态，非常</p>
            <p>的安心，省去很多时间</p>
          </div>
        </div>
        <div 
        class="client-item"
         :class="client3.style"
           @touchstart="ontouchstart($event, 3)"
          @touchmove="ontouchmove($event, 3)"
          @touchend="ontouchend($event, 3)"
          draggable="true"
         >
          <img :src="client3.bg" alt="">
          <div class="top">
            <span>航嘉货运</span>
            <span>陈先生</span>
          </div>
          <div class="text">
            <p>一键订舱，自动对接船公</p>
            <p>司API接口，简化操作的</p>
            <p>工作模式，非常好</p>
          </div>
        </div>
      </div>
      <div class="bg-line"></div>
  </div>
</template>

<script>
import MobileCarousel from "../../components/MobileCarousel";
import CardItem from "./component/CardItem";

import cardIcon1 from "../../../public/static/img/card/icon_01.png";
import cardIcon2 from "../../../public/static/img/card/icon_02.png";
import cardIcon3 from "../../../public/static/img/card/icon_03.png";
import cardIcon4 from "../../../public/static/img/card/icon_04.png";
import cardIcon5 from "../../../public/static/img/card/icon_05.png";

import img1 from "../../../public/static/img/mobile/client_w_a.png";
import img2 from "../../../public/static/img/mobile/client_w.png";
import img3 from "../../../public/static/img/mobile/client_m_a.png";
import img4 from "../../../public/static/img/mobile/client_m.png";

export default {
  name: "app",
  components: {
    MobileCarousel,
    CardItem,
  },
  data() {
    return {
      activeProIndex: 0,
      cardlist: [
        {
          logo: cardIcon1,
          title: '报价查询',
          desc: '实时查询海量船司船代30天最低报价，支持最快，直达等多条件查询'
        },
        {
          logo: cardIcon2,
          title: '在线订舱',
          desc: '无需专业培训和重复数据输入，统一界面，轻松对接所有船司，实时订舱'
        },
        {
          logo: cardIcon3,
          title: '舱单发送',
          desc: '智能加人工双重审核保证，无需二次确认，支持海量船司多家船代'
        },
        {
          logo: cardIcon4,
          title: '票证防伪',
          desc: '量子云码，破坏率90%依然准确识别，完美超越普通二维码'
        },
        {
          logo: cardIcon5,
          title: '智能客服',
          desc: '机器人智能回复用户咨询问题，人工客服与机器人之间轻松切换，相辅相成更高效'
        }
      ],
      img1,
      img2,
      img3,
      img4,
      client1: {
        style: 'musk',
        bg: img1
      },
      client2: {
        style: 'musk1',
        bg: img4
      },
      client3: {
        style: 'musk2',
        bg: img4
      },
      touchX_start: 0,
      touchX_end: 0,
    };
  },
  methods: {
    onSelectedProChange(i) {
      this.activeProIndex = i;
    },
    ontouchstart(e, index) {
      console.log('ontouchstart', e.touches[0]);
      let clientName = 'client' + index
      let client = this[clientName];
      if (client.style !== 'musk') return;
      this.touchX_start = e.touches[0].clientX;
    },
    ontouchmove(e, index) {
      console.log('ontouchmove', e.touches[0]);
      let clientName = 'client' + index
      let client = this[clientName];
      if (client.style !== 'musk') return;
      this.touchX_end = e.touches[0].clientX;
    },
    ontouchend(e, index) {
      console.log('ontouchend', e);
      let change = this.touchX_end - this.touchX_start;
      let clientName = 'client' + index
      let client = this[clientName];
      this.touchX_start = 0;
      this.touchX_end = 0;
      if (client.style !== 'musk') return;
      if (change > 1) {
        if (index === 1) {
          this.client1 = {
            style: 'musk2',
            bg: img2
          }
          this.client2 = {
            style: 'musk',
            bg: img3
          }
          this.client3 = {
            style: 'musk1',
            bg: img4
          }
        }
        
        if (index === 2) {
          this.client1 = {
            style: 'musk1',
            bg: img2
          }
          this.client2 = {
            style: 'musk2',
            bg: img4
          }
          this.client3 = {
            style: 'musk',
            bg: img3
          }
        }

        if (index === 3) {
          this.client1 = {
            style: 'musk',
            bg: img1
          }
          this.client2 = {
            style: 'musk1',
            bg: img4
          }
          this.client3 = {
            style: 'musk2',
            bg: img4
          }
        }
      }
      if (change < -1) {
        if (index === 1) {
          this.client1 = {
            style: 'musk1',
            bg: img2
          }
          this.client2 = {
            style: 'musk2',
            bg: img4
          }
          this.client3 = {
            style: 'musk',
            bg: img3
          }
        }

        if (index === 2) {
          this.client1 = {
            style: 'musk',
            bg: img1
          }
          this.client2 = {
            style: 'musk1',
            bg: img4
          }
          this.client3 = {
            style: 'musk2',
            bg: img4
          }
        }

         if (index === 3) {
          this.client1 = {
            style: 'musk2',
            bg: img2
          }
          this.client2 = {
            style: 'musk',
            bg: img3
          }
          this.client3 = {
            style: 'musk1',
            bg: img4
          }
        }
      }
    }
  }
};
</script>

<style lang="scss" scoped>
