    margin: 0px 20px 30px;
  }
  .client-say {
    position: relative;
    z-index: 0;
    .bg {
      width: 100%;
    }
    .title {
      font-size: 28px;
      text-align: center;
      font-weight: normal;
      margin-bottom: 20px;
      color: #ef7609;
      position: absolute;
      top: 12%;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .client-item {
    position: absolute;
    width: 80%;
    color: #5e2905;
    transition: all 0.5s;
    &.musk {
      left: 50%;
      top: 35%;
      transform: translateX(-50%);
      font-size: 16px;
      z-index: 3;
      transition: all 0.5s;
    }
    &.musk1 {
      width: 70%;
      left: 0px;
      top: 38%;
      font-size: 14px;
      z-index: 2;
      transition: all 0.5s;
    }

    &.musk2 {
      width: 70%;
      right: 0px;
      top: 38%;
      font-size: 14px;
      z-index: 2;
      transition: all 0.5s;
    }

    img {
      width: 100%;
    }
    .top {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: 16%;
      text-align: center;
      span {
        margin-right: 14px;
      }
    }
    .text {
      position: absolute;
      bottom: 18%;
      left: 50%;
      transform: translateX(-50%);
      width: 60%;
      margin-left: 2%;
      text-align: center;
    }
  }
}

@media screen and (max-width: 376px) {
  .page-home {
    .client-say {
      .client-item {
        &.musk {
          font-size: 14px;
        }
        &.musk1 {
          font-size: 12px;
        }

        &.musk2 {
          font-size: 12px;
        }
      }
    }
  }
}
</style>

